$light_blue: #1eacc7;
$peach: #ff5656;
@mixin btn_hover {
    &:hover {
        background-image: none;
        background-color: $light_blue; } }

* {
    font-family: 'Roboto', sans-serif; }

header {
    position: relative;
    height: 51px; }
nav {
    position: fixed;
    width: 100%;
    height: 51px;
    background-color: #212121;
    z-index: 3; }
.menu {
    display: flex;
    justify-content: space-between;
    padding: 13px 70px;
    margin: 0;
    list-style-type: none;
    .menu_link {
        font-size: 15px;
        font-weight: 300;
        color: #ffffff;
        text-decoration: none; }
    .menu_item {
        border-right: 2px dotted black;
        padding-right: 45px; } }
.subheader {
    position: absolute;
    top: 50px;
    width: 100%;
    height: 84px;
    background-color: rgba(0, 0, 0, 0.6);
    .subheader_logo {
        max-width: 100%;
        margin-top: 16px;
        .subheader_logo_text {
            margin-top: 6px;
            opacity: 0.5;
            font-size: 13px;
            font-weight: 100;
            color: #ffffff; } }
    .subheader_numb {
        .subheader_numb_text {
            margin-top: 18px;
            font-size: 12px;
            font-weight: 300;
            color: #ffffff;
            text-align: right; }
        .subheader_numb_numb {
            margin-top: -4px;
            display: block;
            font-size: 22px;
            font-weight: 400;
            text-transform: uppercase;
            color: #ffffff;
            text-align: right; } }
    .subheader_btn {
        margin-top: 16px;
        width: 178px;
        height: 52px;
        background-image: linear-gradient(0.0deg, #1eacc7 0%, #4ce2ff 100%);
        border: none;
        box-shadow: inset 0px -3px 0px 0px rgba(0,0,0,0.11);
        font-size: 14px;
        font-weight: 400;
        text-transform: uppercase;
        text-shadow: 1px 1px 0px rgba(0,0,0,0.2);
        color: #ffffff;
        border-radius: 4px;
        @include btn_hover; } }
.promo {
    min-height: 800px;
    background: url(../img/bg/main_bg.png) center center/cover no-repeat;
    padding: 150px 0 135px 0;
    .promo_text {
        margin-top: 23px;
        font-size: 18px;
        line-height: 24px;
        font-weight: 400;
        color: #ffffff;
        text-align: center; }
    .promo_btn {
        display: block;
        margin: 0 auto;
        margin-top: 115px;
        width: 265px;
        height: 69px;
        background-image: linear-gradient(0.0deg, #1eacc7 0%, #4ce2ff 100%);
        border: none;
        box-shadow: inset 0px -4px 0px 0px rgba(0,0,0,0.11);
        border-radius: 5px;
        font-size: 18px;
        font-weight: 400;
        text-transform: uppercase;
        text-shadow: 1px 1px 0px rgba(0,0,0,0.2);
        color: #ffffff;
        @include btn_hover; }
    h1.promo_header {
            font-size: 60px;
            font-weight: 900;
            text-transform: uppercase;
            color: #ffffff;
            text-align: center;
            line-height: 60px;
            margin: 0; }
    h2.promo_subheader {
            margin: 10px 0 0 0;
            font-size: 36px;
            font-weight: 700;
            text-transform: uppercase;
            color: #ffffff;
            text-align: center;
            line-height: 36px; } }
.label {
    display: block;
    margin: 0 auto;
    width: 112px;
    height: 24px;
    background-color: #000000;
    font-size: 11px;
    line-height: 24px;
    font-weight: 100;
    text-transform: uppercase;
    color: #ffffff;
    text-align: center; }
.title {
    margin-top: 38px;
    font-size: 38px;
    font-weight: 700;
    text-transform: uppercase;
    color: #222222;
    text-align: center; }
.subtitle {
    font-size: 17px;
    font-weight: 400;
    color: #222222;
    text-align: center;
    margin-top: 19px; }
.pros {
    padding-top: 71px;
    min-height: 900px;
    .row {
        margin-top: 40px; }
    .pros_item {
        display: flex;
        align-items: center;
        width: 480px;
        height: 180px;
        .pros_text {
            width: 342px;
            margin-left: 26px;
            line-height: 21px;
            font-size: 15px;
            font-weight: 300;
            color: #202020;
            text-align: justify;
            span {
                font-size: 17px;
                font-weight: 400;
                color: $light_blue;
                text-align-last: left; } } } }
.mobile {
    padding: 48px 0 80px 0;
    min-height: 864px;
    background: url('../img/bg/mobile_bg.png') center center/cover no-repeat;
    .row {
        margin-top: 32px;
        .mobile_item {
            position: relative;
            width: 320px;
            height: 280px;
            margin: 0 auto;
            margin-bottom: 19px;
            padding: 92px 0 35px 0;
            &_1 {
                background: url('../img/mobile/img1.jpg') center center/cover no-repeat; }
            &_2 {
                background: url('../img/mobile/img2.jpg') center center/cover no-repeat; }
            &_3 {
                background: url('../img/mobile/img3.jpg') center center/cover no-repeat; }
            &_4 {
                background: url('../img/mobile/img4.jpg') center center/cover no-repeat; }
            &_5 {
                background: url('../img/mobile/img5.jpg') center center/cover no-repeat; }
            &_6 {
                background: url('../img/mobile/img6.jpg') center center/cover no-repeat; }
            .mobile_item_subtitle {
                font-size: 24px;
                font-weight: 400;
                text-transform: uppercase;
                color: #ffffff;
                text-align: center; }
            .mobile_item_plus {
                display: flex;
                justify-content: center;
                align-items: center;
                position: absolute;
                bottom: 35px;
                left: 50%;
                transform: translateX(-50%);
                padding: 6px;
                width: 26px;
                height: 26px;
                background-color: #000000;
                border-radius: 100%;
                margin: 0 auto;
                cursor: pointer;
                &::before {
                    content: '';
                    display: block;
                    width: 14px;
                    height: 2px;
                    background-color: $light_blue;
                    transition: 0.3s all; }
                &::after {
                    content: '';
                    position: absolute;
                    left: 50%;
                    transform: translateX(-50%);
                    display: block;
                    width: 2px;
                    height: 14px;
                    background-color: $light_blue;
                    transition: 0.3s all; }
                &:hover {
                    &:before,&:after {
                        background-color: $peach; } } } } } }

.choice {
    position: relative;
    min-height: 896px;
    .choice_dark {
        padding-top: 57px;
        height: 499px;
        background-color: #070716;
        .label {
            background-color: #fff;
            color: black; }
        .title {
            margin-top: 27px;
            color: white; }
        .subtitle {
            margin-top: 5px;
            color: white; } }
    .choice_light {
        padding-top: 217px;
        background: url('../img/bg/choice_bg.png') center center/cover no-repeat;
        min-height: 397px;
        .choice_light_text {
            color: #222222;
            font-size: 17px;
            font-weight: 300;
            text-align: center; } }
    .choice_img {
            position: absolute;
            top: 232px;
            left: 50%;
            transform: translateX(-50%); } }
.require {
    padding: 68px 0 84px 0;
    .require_block {
        width: 478px;
        margin-top: 33px;
        .require_title {
            font-size: 22px;
            font-weight: 400;
            color: $light_blue; }
        .require_list {
            margin-top: 10px;
            padding-left: 50px;
            list-style-type: none;
            font-size: 17px;
            font-weight: 300;
            color: #222222;
            text-align: justify;
            li {
                position: relative;
                margin-bottom: 10px;
                &:before {
                    content: '';
                    position: absolute;
                    display: block;
                    left: -32px;
                    top: 13px;
                    transform: translateY(-50%);
                    width: 21px;
                    height: 20px;
                    background: url(../icons/check_mark_blue.png) center center/cover no-repeat; } } }
        .require_descr {
            font-size: 17px;
            font-weight: 300;
            color: $light_blue; } }

    .peach {
        margin-top: 62px;
        .require_title {
            color: $peach; }
        .require_list {
            li {
                &:before {
                    background: url(../icons/check_mark_red.png) center center/cover no-repeat; } } } } }
.cities {
    padding-top: 49px;
    background: url('../img/bg/map_bg.jpg') center center/cover no-repeat;
    position: relative;
    .container {
        min-height: 717px;
        .label {
            background-color: #fff;
            color: black; }
        .title {
            margin-top: 27px;
            color: white; }
        .subtitle {
            margin-top: 5px;
            color: white; }
        .cities_link {
            position: absolute;
            left: 50%;
            transform: translateX(-50%);
            bottom: 49px;
            a {
                text-decoration: none;
                color: $light_blue;
                text-transform: uppercase; } } } }
footer {
    min-height: 156px;
    padding-top: 14px;
    position: relative;
    .footer_divider {
        display: block;
        margin: 0 auto;
        width: 299px;
        height: 4px;
        background-color: $light_blue; }
    .footer_nav {
        display: block;
        margin: 0 auto;
        margin-top: 34px;
        width: 390px;
        min-height: 89px;
        &_main {
            display: flex;
            justify-content: space-around;
            a {
                text-decoration: none;
                font-size: 15px;
                font-weight: 300;
                text-transform: uppercase;
                color: #070716; } }
        &_scnd {
            display: flex;
            justify-content: space-around;
            a {
                text-decoration: none;
                font-size: 11px;
                font-weight: 300;
                text-transform: uppercase;
                color: #999999; } }
        .footer_language {
            margin-top: 17px;
            position: absolute;
            left: 50%;
            transform: translateX(-50%);
            font-size: 11px;
            font-weight: 700;
            text-transform: uppercase;
            color: $light_blue; } }
    .footer_social {
        position: absolute;
        top: 51px;
        left: 23px;
        display: flex;
        justify-content: start;
        &_logo {
            position: relative;
            margin-right: 9px;
            width: 32px;
            height: 32px;
            background-color: black;
            border-radius: 100%;
            i {
                position: absolute;
                left: 50%;
                top: 50%;
                transform: translate(-50%, -50%);
                color: white; } } }
    .footer_dwnload {
        position: absolute;
        top: 51px;
        right: 23px;
        display: flex;
        justify-content: end;
        &_logo {
            margin-right: 5px;
            width: 95px;
            height: 27px;
            img {
                width: 100%; } } } }
.botline {
    height: 8px;
    background-color: #070716; }

